<template name="user_demand_list">
	<view class="user_demand_list things_width_full things_padding_100">
        <!-- tabbar -->
        <cu-custom><block slot="content">我的发布需求</block></cu-custom>
		<scroll-view scroll-y class="things_width_full page">
            <view class="demand_type_box">
                <view class="bg-img flex justify-between align-center things_width_full demand_find demand_find_car" style="background-image:url('./../../../../../static/images/publish/bg_car.png')" @tap="linkToDemandCar">
                    <view class="things_width_full flex justify-start align-center box_top">
                        <image class="demand_find_icon" src="./../../../../../static/images/publish/huoche.png" mode="widthFix" />
                        <view class="demand_find_title">发布的车辆</view>
                    </view>
                    <view class="things_width_full flex justify-start align-center box_bottom">
                        <image class="demand_demand_icon" src="./../../../../../static/images/global/stamp.png" mode="widthFix" />
                        <view class="demand_demand_title">查看车辆</view>
                    </view>
                </view>
                <view class="bg-img flex justify-start align-center things_width_full demand_find demand_find_freight" style="background-image:url('./../../../../../static/images/publish/bg_freight.png')" @tap="linkToDemandFreight">
                    <view class="things_width_full flex justify-start align-center box_top">
                        <image class="demand_find_icon" src="./../../../../../static/images/publish/huowu.png" mode="widthFix" />
                        <view class="demand_find_title">发布的货物</view>
                    </view>
                    <view class="things_width_full flex justify-start align-center box_bottom">
                        <image class="demand_demand_icon" src="./../../../../../static/images/global/stamp.png" mode="widthFix" />
                        <view class="demand_demand_title">查看货物</view>
                    </view>
                </view>
            </view>
		</scroll-view>
	</view>
</template>

<script>
	export default {
		name: "publish",
		data() {
			return {
				
			};
        },
        methods: {
            /**
             * @description: 发布的车辆
             * @param {*} e
             * @return {*}
             * @author: gitxinghui
             */
            linkToDemandCar(e) {
                uni.navigateTo({
					url: `/pages/things/user/demand/car/list/index`,
					fail: function(err) {
						console.log(err);
					}
				});
            },
            /**
             * @description: 发布的货物
             * @param {*} e
             * @return {*}
             * @author: gitxinghui
             */
            linkToDemandFreight(e) {
                uni.navigateTo({
					url: `/pages/things/user/demand/freight/list/index`,
					fail: function(err) {
						console.log(err);
					}
				});
            }
        },
		onShow() {
			console.log("success")
		}
	}
</script>

<style lang="scss" scoped>
	@import "./index.scss";
</style>